export class Geometry
{
	// lineIntersectsLine(){}
	// lineIntersectsQuad(){}

	static negSafeMod(a,b)
	{
		if(a >= 0) return a % b;
		return (b + a % b) % b;
	}

	static quadIntersectsQuad(x1, y1, w1, h1, x2, y2, w2, h2)
	{
		const left1   = x1;
		const right1  = x1 + w1;
		const top1    = y1;
		const bottom1 = y1 + h1;

		const left2   = x2;
		const right2  = x2 + w2;
		const top2    = y2;
		const bottom2 = y2 + h2;

		if(right1 < left2 || left1 > right2 || bottom1 < top2 || top1 > bottom2)
		{
			return false;
		}

		return true;
	}
}
